$(document).ready(function(){

    $('.mobile-slide').slick({
        infinite: true,
        responsive: [
            {
              breakpoint: 992,
              settings: {
                arrows: true,
                prevArrow: '<button type="button" class="slick-prev arrows"><img src="icons/arrow-prev.png"></button>',
                nextArrow: '<button type="button" class="slick-next arrows"><img src="icons/arrow-next.png"></button>',
                // centerMode: true,
                centerPadding: '40px',
                slidesToShow: 3
              }
            },
            {
              breakpoint: 480,
              settings: {
                arrows: false,
                centerPadding: '40px',
                slidesToShow: 1,
                adaptiveHeight: true,
                centerMode: true,
                variableWidth: true,
                dots: true
              }
            }
          ]
    });

    // light scroll
    $(".menu, button, .footer__nav").on("click","a", function (event) {
        event.preventDefault();
        var id  = $(this).attr('href'),
            top = $(id).offset().top;
        $('body,html').animate({scrollTop: top}, 1500);
    });



    // send form
    $('form').submit(function(e) {
        e.preventDefault();
        $.ajax({
            type: "POST",
            url: "mailer/smart.php",
            data: $(this).serialize()
        }).done(function() {
            $(this).find("input").val("");
            $('form, .appointment .title, .appointment__info, .appointment__require').fadeOut();
            $('body').css({'overflow' : 'hidden'});
            $(".overlay").fadeIn('slow');

            $('form').trigger('reset');
        });
        return false;
    });

    $('.close').on('click', function() {
        $(".overlay").fadeOut();
        $('body').css({'overflow' : ''});
        $('form, appointment title, appointment__info, .appointment__require').fadeIn();
    });




});
